<template>
	<div>
		<audio :src="audioplay" autoplay="" controls=""></audio>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				audioplay:''
			}
		},
		watch:{
			'$route.params'(a,b){
				if(a.songmid){
					if(a.songmid!=0 || a.songname!=0){
						this.$http.jsonp('https://c.y.qq.com/base/fcgi-bin/fcg_music_express_mobile3.fcg?g_tk=1278911659&hostUin=0&format=jsonp&callback=callback&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq&needNewCode=0&cid=205361747&uin=0&songmid='+a.songmid+'&filename=C400'+a.songmid+'.m4a&guid=3655047200',{jsonpCallback:'callback'}).then((data)=>{
							var filename=data.body.data.items[0].filename
							var vkey=data.body.data.items[0].vkey
							this.audioplay='http://dl.stream.qqmusic.qq.com/'+filename+'?vkey='+vkey+'&guid=3655047200&fromtag=66'
						})
					}
				}
			}
		}
	}
</script>

<style>
</style>


