<template>
	<div>
		<ul>
			<li v-for='i in playlist' style="line-height: 20px;border-bottom:solid 1px #ddd;">
				<a class="pp" :href="'#/list/'+i.songmid+'/'+i.songname" style="text-decoration: none;color: black;">{{i.songname}}</a>
			</li>
		</ul>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				playlist:[],
				num:0
			}
		},
		created(){
			if(this.$route.params.songmid){
				if(this.$route.params.songmid!=0||this.$route.params.songname!=0){
					this.playlist.push(this.$route.params)
				}
			}
			
		},
		
		watch:{
			'$route.params'(a,b){
				if(a.songmid){
					if(a.songmid!=0&&a.songname!=0){
						var type=true
						for(var i=0;i<this.playlist.length;i++){
							if(this.playlist[i].songmid==a.songmid){
								type=false
							}
						}
						if(type){
							this.playlist.push(a)
						}
					}
				}
			}
		}
	}
</script>

<style>
</style>


