<template>
  <div id="app">
    <div style="height: 60px;background: #045550;margin: 0 50px;">
      <div style="padding:0 20px;float:left;line-height: 60px;" v-for="i in arr">
        <a :href="i.path" style="color: white;text-decoration: none;">{{i.title}}</a>
      </div>
    </div>
    <div style="min-height: 500px;margin:0 50px;">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>
    <div
      style="height: 100px;background: #045550;margin: 0 50px;padding-top: 20px;box-sizing: border-box;text-align: center;"
    >
      <play></play>
    </div>
  </div>
</template>
<script>
import play from "./routes/play.vue";
export default {
  name: "app",
  components: {
    play
  },
  data() {
    return {
      arr: [
        { title: "首页", path: "#/" },
        { title: "列表", path: "#/list/0/0" }
      ]
    };
  }
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
  list-style: none;
}
</style>



