<template>
	<div>
		<div style="text-align: center;line-height: 30px;padding:20px 0;">
			<input type="text" style="width:80%;height:30px;" placeholder="请输入歌名/歌手" v-model='value' @keyup.enter="search"/>
		</div>
		<div style="min-height: 400px;width:80%;margin:0 auto;width:80%;">
			<h4>搜索列表：</h4>
			<ul class="index_list" v-if='indexlist' style="width:100%;">
				<li v-for='i in indexlist' style="width:100%;border-bottom:solid 1px #ddd;">
					<a class="om" :href="'#/list/'+i.songmid+'/'+i.songname" style="width:100%;text-decoration: none;color: black;">{{i.songname}}</a>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				value:'',
				indexlist:[],
				searchlist:''
			}
		},
		created(){
			this.$http.jsonp('https://c.y.qq.com/splcloud/fcgi-bin/gethotkey.fcg?g_tk=5381&uin=0&format=jsonp&jsonpCallback=callback&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&_=1513317614040',{jsonpCallback:'callback'}).then((data)=>{
				this.searchlist=data.body.data.hotkey
			})
		},
		methods:{
			search(){
				this.$http.jsonp('https://c.y.qq.com/soso/fcgi-bin/client_search_cp?g_tk=5381&p=1&n=20&w='+this.value+'&format=jsonp&jsonpCallback=callback&loginUin=0&hostUin=0&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq&needNewCode=0&remoteplace=txt.yqq.song&t=0&aggr=1&cr=1&catZhida=1&flag_qc=0',{jsonpCallback:'callback'}).then((data)=>{
					this.indexlist=data.data.data.song.list
				})
			},
			
		}
	}
</script>

<style>
	.index_list li{
		line-height: 30px;
	}
	.index_list li.index_active a{
		color:red !important;
	}
</style>


